import styles from "./Answer.module.css";
import cn from "classnames";

const Answer = ({a, b, c}) => {

    const calcX = (a = 0, b = 0, c = 0) => {
        const discriminant = (b*b) - (4 * a * c);
        if(discriminant < 0){
            console.log("Корней нет");
            return discriminant
        }else{
            const x1 = ((-b + Math.sqrt(discriminant))/(2 * a)).toFixed(5);
            const x2 = ((-b - Math.sqrt(discriminant))/(2 * a)).toFixed(5);
            return {
                x1,
                x2,
                discriminant
            };
        }
    }

    const gcd_two_numbers = (x, y) => {
        if ((typeof x !== 'number') || (typeof y !== 'number'))
            return false;
        x = Math.abs(x);
        y = Math.abs(y);
        while(y) {
            let t = y;
            y = x % y;
            x = t;
        }
        return x;
    }



    let answer = calcX(a,b,c);

    const gdcx1 = gcd_two_numbers(-b + Math.sqrt(answer.discriminant), 2*a);
    const gdcx2 = gcd_two_numbers(-b - Math.sqrt(answer.discriminant), 2*a);

    console.log(gdcx1, gdcx2)


    console.log(answer);

    const contentAnswer = (
        <>
            Решение:
            <div className={styles.formula}>
                {answer.discriminant} = {b}<sup>2</sup> - ({4*a*c})
            </div>
            <div className={cn(styles.formula, styles.hasFraction)}>
                x<sub>1,2</sub>= <div className={styles.fraction}><span>{b} ± √{answer.discriminant}</span><span>{2*a}</span></div>
            </div>
            <div className={styles.hasResolve}>
                <div>x<sub>1</sub>
                    = <div className={styles.fraction}>{answer.x1}</div>
                    = <div className={styles.fraction}><span>{(-b + Math.sqrt(answer.discriminant)) / gdcx1}</span><span>{2*a / gdcx1}</span></div>
                </div>
                <div>x<sub>2</sub>
                    = <div className={styles.fraction}>{answer.x2}</div>
                    = <div className={styles.fraction}><span>{(-b - Math.sqrt(answer.discriminant)) / gdcx2}</span><span>{2*a / gdcx2}</span></div>
                </div>
            </div>
        </>
    )

    return (
        <div className={styles.Answer}>
            <div className={styles.theoretical}>
                Формула:
                <div className={styles.formula}>
                    D = b<sup>2</sup> - 4ac
                </div>
                <div className={cn(styles.formula, styles.hasFraction)}>
                    x<sub>1,2</sub>= <div className={styles.fraction}><span>b ± √D</span><span>2a</span></div>
                </div>
            </div>

            <div className={styles.solve}>
                { answer.discriminant > -1 ?  contentAnswer : "Нет решений :("}
            </div>
        </div>
    );
};

export default Answer;
